@import url('reset.css');

  #app {
    font-family: 'Noto Sans', sans-serif;
    color: #222831;
    height: auto;
    background-color: #FFFFFF;
  }

  a {
    text-decoration: none;
    color: #222831;
  }

  /* 
  HERO SECTION
  --------------------------------------------*/
  .hero {
    height: 300px;
    background-image: url('../../assets/img/hall-sm.jpg');
    background-size: cover;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .logo {
    width: 170px;
    height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 5px solid #FFFFFF;
    border-radius: 50%;
    margin-top: 40px;
  }
  .logo h2 {
    text-transform: uppercase;
    color: #FFF;
    font-size: 1.8em;
    font-family: 'Saira Condensed', sans-serif;
  }
  .logo h1 {
    text-transform: uppercase;
    color: #FFF;
    font-size: 4em;
    font-family: 'Saira Condensed', sans-serif;
  }
  .hero h2 span {
    color: #41B883;
  }
  .hero h3 {
    color: #FFFFFF;
    text-align: center;
    font-size: 1.2em;
    position: absolute;
    bottom: 20px;
  }

  /* 
  NAVIGATION
  --------------------------------------------*/
  nav ul {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    height: 100px;
  }
  nav ul li {
    text-transform: uppercase;
    font-family: 'Saira Condensed', sans-serif;
  }
  nav ul li a {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
  }
  nav ul li:hover a {
    background-color: #222831;
    color: #FFF;
    transition: all .3s;
  }
  nav ul li .fa {
    margin: 10px;
    font-size: 1.5em;
  }
  nav ul li:first-child {
    background-color: #AEC176;
  }
  nav ul li:nth-child(2) {
    background-color: #F3D179;
  }
  nav ul li:nth-child(3) {
    background-color: #F09872;
  }
  nav ul li:last-child {
    background-color: #F46060;
  }

  /* 
  PAGES
  --------------------------------------------*/

  .page {
      height: 100%;
  }
  .page h1 {
      font-size: 1.5em;
      text-transform: uppercase;
      margin-bottom: 20px;
      padding: 20px;
  }
  .page .wrap {
    padding: 20px;
  }
  .page p {
    padding: 10px 0;
    line-height: 1.7em;
  }

  .home h1 {
      background-color: #AEC176;
  }
  .about h1 {
      background-color: #F3D179;
  }
  .speakers h1 {
      background-color: #F09872;
  }
  .venue h1 {
      background-color: #F46060;
  }

  /* 
  FOOTER
  --------------------------------------------*/
  footer {
    padding: 2em;
    text-align: center;
    background-color: #222831;
    color: #FFFFFF;
  }
  footer p {
    font-size: .9em;
  }
  footer a {
    color: #3AB982;
  }

  /* 
  MEDIA QUERIES
  --------------------------------------------*/

  @media screen and (min-width: 800px) {
    #app {
      width: 800px;
      margin: 0 auto;
    }
    nav ul {
      height: 150px;
    }
    nav ul li .fa, nav ul li a {
      font-size: 1.5em;
    }
    .speaker {
      width: 75%;
      margin: 0 auto;
    }
    .venue figure {
      height: 360px !important;
    }
  }


